@-webkit-keyframes astronaut-sway {
    0%{background-position:center}
    25%{background-position: 0% 0%;}
    50%{background-position: 100% 0%}
    75%{background-position:100% 100%} 
    100%{background-position:center}
  }
  @keyframes astronaut-sway {
    0%{background-position:center}
    25%{background-position: 0% 0%;}
    50%{background-position: 100% 0%}
    75%{background-position:100% 100%} 
    100%{background-position:center}
  }
@-webkit-keyframes astronaut-sway-2 {
    0%{background-position:center}
    25%{background-position: 0% 100%;}
    50%{background-position: 100% 0%}
    75%{background-position:50% 100%} 
    100%{background-position:center}
  }
  @keyframes astronaut-sway-2 {
    0%{background-position:center}
    25%{background-position: 0% 100%;}
    50%{background-position: 100% 0%}
    75%{background-position:50% 100%} 
    100%{background-position:center}
  }
@-webkit-keyframes astronaut-sway-3 {
    0%{background-position:center}
    25%{background-position: 100% 0%;}
    50%{background-position: 100% 50%}
    75%{background-position: 0% 0%} 
    100%{background-position:center}
  }
  @keyframes astronaut-sway-3 {
    0%{background-position:center}
    25%{background-position: 100% 0%;}
    50%{background-position: 100% 50%}
    75%{background-position: 0% 0%} 
    100%{background-position:center}
  }
  @-webkit-keyframes astronaut-rotate {
    0%{transform: rotate(0deg);}
    25%{transform: rotate(90deg);}
    50%{transform: rotate(180deg);}
    75%{transform: rotate(270deg);} 
    100%{transform: rotate(360deg);}
  }
  @keyframes astronaut-rotate {
    0%{transform: rotate(0deg);}
    25%{transform: rotate(90deg);}
    50%{transform: rotate(180deg);}
    75%{transform: rotate(270deg);} 
    100%{transform: rotate(360deg);}
  }
  @-webkit-keyframes astronaut-rotate-invert {
    0%{transform: rotate(0deg);}
    25%{transform: rotate(-90deg);}
    50%{transform: rotate(-180deg);}
    75%{transform: rotate(-270deg);} 
    100%{transform: rotate(-360deg);}
  }
  @keyframes astronaut-rotate-invert {
    0%{transform: rotate(0deg);}
    25%{transform: rotate(-90deg);}
    50%{transform: rotate(-180deg);}
    75%{transform: rotate(-270deg);} 
    100%{transform: rotate(-360deg);}
  }
